import React, { useState } from 'react';

type Channel = 'sms' | 'email' | 'phone' | 'facebook';

type Contact = {
  name: string;
  phone: string;
  phone2?: string;
  email: string;
  email2?: string;
  facebook: string;
  preferredChannel: Channel;
  vehicleOfInterest?: {
    ymm: string;
    stock: string;
    vin: string;
    price: string;
  };
  tradeInVehicle?: {
    ymm: string;
    condition: string;
    mileage: string;
    estimatedValue: string;
  };
  aiSummary?: {
    conversationSummary: string;
    actionItems: string[];
  };
};

type Props = {
  open: boolean;
  onClose: () => void;
  onOpen: () => void;
  contact: Contact | null;
};

const ContactProfileDrawer: React.FC<Props> = ({ open, onClose, contact }) => {
  const [preferredChannel, setPreferredChannel] = useState<Channel>(contact?.preferredChannel || 'sms');

  const channelOptions = [
    { value: 'sms' as Channel, label: 'SMS', icon: '📱' },
    { value: 'email' as Channel, label: 'Email', icon: '✉️' },
    { value: 'phone' as Channel, label: 'Phone', icon: '📞' },
    { value: 'facebook' as Channel, label: 'Facebook', icon: '💬' },
  ];

  return (
    <div
      style={{
        position: 'fixed',
        top: 0,
        right: open ? 0 : -320,
        width: 320,
        height: '100vh',
        background: '#fff',
        boxShadow: open ? '-2px 0 16px rgba(0,0,0,0.10)' : 'none',
        transition: 'right 0.3s cubic-bezier(.4,0,.2,1)',
        zIndex: 1500,
        display: 'flex',
        flexDirection: 'column',
        borderLeft: '1.5px solid #ececec',
      }}
      aria-hidden={!open}
    >
      <div style={{ padding: '24px 24px 12px 24px', borderBottom: '1px solid #ececec', display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
        <span style={{ fontWeight: 700, fontSize: '1.2em' }}>Contact Profile</span>
        <button onClick={onClose} style={{ background: 'none', border: 'none', fontSize: 20, cursor: 'pointer', color: '#888' }} title="Close">×</button>
      </div>
      {contact ? (
        <div style={{ padding: '24px', flex: 1, display: 'flex', flexDirection: 'column', gap: 18, overflowY: 'auto' }}>
          <div style={{ fontSize: '1.15em', fontWeight: 600, marginBottom: 8 }}>{contact.name}</div>
          
          {/* Contact Information */}
          <div style={{ marginBottom: 16 }}>
            <div style={{ fontWeight: 600, marginBottom: 8, color: '#374151' }}>Contact Information</div>
            <div style={{ display: 'flex', alignItems: 'center', marginBottom: 6 }}>
              <span style={{ marginRight: 8 }}>📞</span>
              <span style={{ color: '#2563eb', fontWeight: 500 }}>{contact.phone}</span>
            </div>
            {contact.phone2 && (
              <div style={{ display: 'flex', alignItems: 'center', marginBottom: 6 }}>
                <span style={{ marginRight: 8 }}>📞</span>
                <span style={{ color: '#2563eb', fontWeight: 500 }}>{contact.phone2}</span>
              </div>
            )}
            <div style={{ display: 'flex', alignItems: 'center', marginBottom: 6 }}>
              <span style={{ marginRight: 8 }}>✉️</span>
              <span style={{ color: '#2563eb', fontWeight: 500 }}>{contact.email}</span>
            </div>
            {contact.email2 && (
              <div style={{ display: 'flex', alignItems: 'center', marginBottom: 6 }}>
                <span style={{ marginRight: 8 }}>✉️</span>
                <span style={{ color: '#2563eb', fontWeight: 500 }}>{contact.email2}</span>
              </div>
            )}
            <div style={{ display: 'flex', alignItems: 'center' }}>
              <span style={{ marginRight: 8 }}>💬</span>
              <span style={{ color: '#2563eb', fontWeight: 500 }}>{contact.facebook}</span>
            </div>
          </div>

          {/* Preferred Channel */}
          <div style={{ marginBottom: 16 }}>
            <div style={{ fontWeight: 600, marginBottom: 8, color: '#374151' }}>Preferred Channel</div>
            <div style={{ display: 'flex', gap: 8 }}>
              {channelOptions.map(option => (
                <button
                  key={option.value}
                  onClick={() => setPreferredChannel(option.value)}
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: 4,
                    padding: '6px 12px',
                    borderRadius: 6,
                    border: '1px solid #d1d5db',
                    background: preferredChannel === option.value ? '#2e7dff' : '#fff',
                    color: preferredChannel === option.value ? '#fff' : '#374151',
                    cursor: 'pointer',
                    fontSize: '0.9em',
                    fontWeight: 500,
                    transition: 'all 0.15s'
                  }}
                >
                  <span>{option.icon}</span>
                  <span>{option.label}</span>
                </button>
              ))}
            </div>
          </div>

          {/* Vehicle of Interest */}
          {contact.vehicleOfInterest && (
            <div style={{ marginBottom: 16, padding: '14px 16px', background: '#f5f6fa', borderRadius: 10, border: '1px solid #e0e0e0' }}>
              <div style={{ fontWeight: 600, marginBottom: 8, color: '#374151' }}>Vehicle of Interest</div>
              <div style={{ fontSize: '0.9em', lineHeight: 1.5 }}>
                <div><b>YMM:</b> {contact.vehicleOfInterest.ymm}</div>
                <div><b>Stock #:</b> {contact.vehicleOfInterest.stock}</div>
                <div><b>VIN:</b> {contact.vehicleOfInterest.vin}</div>
                <div><b>Price:</b> {contact.vehicleOfInterest.price}</div>
              </div>
            </div>
          )}

          {/* Trade-in Vehicle */}
          {contact.tradeInVehicle && (
            <div style={{ marginBottom: 16, padding: '14px 16px', background: '#fef3c7', borderRadius: 10, border: '1px solid #f59e0b' }}>
              <div style={{ fontWeight: 600, marginBottom: 8, color: '#92400e' }}>Trade-in Vehicle</div>
              <div style={{ fontSize: '0.9em', lineHeight: 1.5 }}>
                <div><b>YMM:</b> {contact.tradeInVehicle.ymm}</div>
                <div><b>Condition:</b> {contact.tradeInVehicle.condition}</div>
                <div><b>Mileage:</b> {contact.tradeInVehicle.mileage}</div>
                <div><b>Est. Value:</b> {contact.tradeInVehicle.estimatedValue}</div>
              </div>
            </div>
          )}

          {/* AI Contact Summary */}
          {contact.aiSummary && (
            <div style={{ marginBottom: 16, padding: '14px 16px', background: '#ecfdf5', borderRadius: 10, border: '1px solid #10b981' }}>
              <div style={{ fontWeight: 600, marginBottom: 8, color: '#065f46' }}>AI Contact Summary</div>
              <div style={{ fontSize: '0.9em', lineHeight: 1.5, marginBottom: 12 }}>
                <div style={{ marginBottom: 8 }}>
                  <b>Conversation Summary:</b>
                  <div style={{ marginTop: 4, color: '#374151' }}>{contact.aiSummary.conversationSummary}</div>
                </div>
                <div>
                  <b>Action Items:</b>
                  <ul style={{ marginTop: 4, marginLeft: 16, color: '#374151' }}>
                    {contact.aiSummary.actionItems.map((item, index) => (
                      <li key={index}>{item}</li>
                    ))}
                  </ul>
                </div>
              </div>
            </div>
          )}
        </div>
      ) : (
        <div style={{ padding: 24, color: '#888' }}>No contact selected.</div>
      )}
    </div>
  );
};

export default ContactProfileDrawer; 