import React from 'react';
import './ConversationList.css';

type Channel = 'all' | 'sms' | 'email' | 'phone' | 'facebook';

type Conversation = {
  id: string;
  name: string;
  lastMessage: string;
  unread?: boolean;
  channel: Channel;
};

type Props = {
  conversations: Conversation[];
  selectedConversationId: string | null;
  onSelect: (id: string) => void;
};

const ConversationList: React.FC<Props> = ({ conversations, selectedConversationId, onSelect }) => (
  <div className="conversation-list">
    <div className="conversation-list-header">Contacts</div>
    {conversations.map(conv => (
      <div
        key={conv.id}
        className={`conversation-item${conv.id === selectedConversationId ? ' selected' : ''}`}
        onClick={() => onSelect(conv.id)}
      >
        <div className="conversation-name">{conv.name}</div>
        <div className="conversation-last">{conv.lastMessage}</div>
        {conv.unread && <span className="conversation-unread">●</span>}
      </div>
    ))}
  </div>
);

export default ConversationList; 